<script>
import Vue from "vue";
var echarts = require("echarts");
Vue.prototype.$echarts = echarts;

export default {
  mounted() {
    this.axios.defaults.withCredentials = true;
    this.getAdminList();
    this.getUpList();
    this.getUpCheck();
    this.getMatchCheck();
    this.getMatchList();
    this.getFeedbackList();
    this.getDownList();
    this.getChartInfo();
    this.getPair();
    this.getConfig();
  },
  methods: {
    refresh: function() {
      this.getAdminList();
      this.getUpList();
      this.getUpCheck();
      this.getMatchCheck();
      this.getMatchList();
      this.getFeedbackList();
      this.getDownList();
      this.getChartInfo();
      this.getPair();
      this.getConfig();
    },
    weibo: function(){
      
    },
    confirmTime: function() {
      this.axios
        .post("/public/admin/command/setconfig", {
          hour: this.timeValue.getHours(),
          minute: this.timeValue.getMinutes(),
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message("修改成功");
          } else {
            this.$message("修改失败，msg：" + res.data.msg);
          }
        });
    },
    setPrice: function(type) {
      if (type == 1) {
        //1为男性金额
        this.axios
          .post("/public/admin/command/setamount", {
            man: this.price * 100,
          })
          .then((res) => {
            if (res.data.code == 0) {
              this.$message("设置成功");
            } else {
              this.$message("修改失败，msg：" + res.data.msg);
            }
          });
      } else if (type == 0) {
        //0为女性金额
        this.axios
          .post("/public/admin/command/setamount", {
            woman: this.price * 100,
          })
          .then((res) => {
            if (res.data.code == 0) {
              this.$message("设置成功");
            } else {
              this.$message("修改失败，msg：" + res.data.msg);
            }
          });
      }
    },
    getPair: function() {
      this.axios.post("/public/admin/command/active").then((res) => {
        console.log("pair:", res.data.data);
        if (res.data.code == 0) {
          this.pair = res.data.data;
        } else {
          this.$message("获取配对列表失败，msg：" + res.data.msg);
        }
      });
    },
    getUpList: function() {
      this.axios
        .post("/public/admin/pass/check", {
          type: 0,
          pass: 1,
        })
        .then((res) => {
          console.log(res);
          if (res.data.code == 0) {
            this.actor2 = res.data.data;
          } else {
            this.$message("获取已上墙列表失败，msg ：" + res.data.msg);
          }
        });
    },
    getAdminList: function() {
      this.axios.defaults.withCredentials = true;
      this.axios.post("/public/admin/admin/listall").then((res) => {
        console.log(res);
        if (res.data.code == 0) {
          this.adminList = res.data.data;
        } else {
          this.$message("获取管理员列表失败，msg ：" + res.data.msg);
        }
      });
    },
    getUpCheck: function() {
      this.axios
        .post("/public/admin/pass/check", {
          type: 0,
          pass: 0,
        })
        .then((res) => {
          console.log(res);
          if (res.data.code == 0) {
            this.actor1 = res.data.data;
          } else {
            this.$message("获取上墙审核列表失败，msg ：" + res.data.msg);
          }
        });
    },
    getMatchCheck: function() {
      this.axios
        .post("/public/admin/pass/check", {
          type: 1,
          pass: 0,
        })
        .then((res) => {
          console.log(res);
          if (res.data.code == 0) {
            this.actor3 = res.data.data;
          } else {
            this.$message("获取互选审核列表失败，msg ：" + res.data.msg);
          }
        });
    },
    getMatchList: function() {
      this.axios
        .post("/public/admin/pass/check", {
          type: 1,
          pass: 1,
        })
        .then((res) => {
          console.log(res);
          if (res.data.code == 0) {
            this.actorMatch = res.data.data;
          } else {
            this.$message("获取参与互选列表失败，msg ：" + res.data.msg);
          }
        });
    },
    getDownList: function() {
      this.axios
        .post("/public/admin/pass/check", {
          type: 0,
          pass: -1,
        })
        .then((res) => {
          console.log(res);
          if (res.data.code == 0) {
            this.actor6 = res.data.data;
          } else {
            this.$message("获取下墙列表失败，msg ：" + res.data.msg);
          }
        });
    },
    getFeedbackList: function() {
      this.axios.post("/public/admin/pass/getsuggestion").then((res) => {
        if (res.data.code == 0) {
          this.actor5 = res.data.data;
        } else {
          this.$message("获取反馈意见失败，msg：" + res.data.msg);
        }
      });
    },
    match: function() {
      window.location.href =
        "http://www.scgxtd.cn/public/admin/command/outexcel";
    },
    confirmD: function() {
      this.axios
        .post("/public/admin/command", {
          default: this.defaultNum,
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message("修改成功");
          } else {
            this.$message("修改失败，msg：" + res.data.msg);
          }
        });
    },
    confirmT: function() {
      this.axios
        .post("/public/admin/command", {
          tomorrow: this.tomorrowNum,
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message("修改成功");
          } else {
            this.$message("修改失败，msg：" + res.data.msg);
          }
        });
    },
    getConfig: function() {
      this.axios.post("/public/admin/command/getconfig").then((res) => {
        if (res.data.code == 0) {
          console.log("get config", res.data.data);
          if (res.data.data.tomorrow) {
            this.tomorrowNum = res.data.data.tomorrow;
          }
          this.defaultNum = res.data.data.default;
          console.log(this.defaultNum);
        } else {
          this.$message("获取人数设置失败，msg：" + res.data.msg);
        }
      });
    },
    handleEditMatchList(index, row) {
      console.log(index, row);
      this.form.openid = row.ID;
      this.target = "/public/admin/command/actchange";
    },
    handleEditShowList(index, row) {
      console.log(index, row);
      this.form.openid = row.ID;
      console.log(this.form);
      this.target = "/public/admin/command/change";
    },
    upLoadEdit() {
      this.axios.post(this.target, this.form).then((res) => {
        if (res.data.code == 0) {
          this.$message("修改成功");
          this.getUpList();
          this.getUpCheck();
          this.getMatchList();
          this.getMatchCheck();
        } else {
          this.$message("修改失败，msg：" + res.data.msg);
        }
      });
    },
    handleDelete(index, row, type) {
      //type=0 为普通上墙,1为互选上墙
      console.log(index, row, type);
      this.axios
        .post("/public/admin/command/delete", {
          openid: row.ID,
          type: type,
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message("操作成功!");
            this.getUpList();
            this.getUpCheck();
            this.getMatchList();
            this.getMatchCheck();
          } else {
            this.$message("操作失败，msg：" + res.data.msg);
          }
        });
    },
    handleDown(index, row) {
      console.log(index, row);
      this.axios
        .post("/public/admin/command/delete", {
          openid: row.ID,
          type: 0,
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message("下墙成功");
            this.getUpList();
          } else {
            this.$message("下墙失败，msg：" + res.data.msg);
          }
        });
    },
    handleRead(index, row) {
      console.log(index, row);
      this.axios
        .post("/public/admin/pass/dealsuggestion", {
          ID: row.ID,
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message("处理成功");
            this.getFeedbackList();
          } else {
            this.$message("处理失败，msg：" + res.data.msg);
          }
        });
    },

    handleDeleteAdmin(index, row) {
      console.log(index, row);
      if (row.ID == 1) {
        this.$message("你不能删除超级管理员");
        return;
      }
      this.$alert("确认要删除管理员" + row.ID + "吗", "提示", {
        confirmButtonText: "确定",
        callback: (action) => {
          if (action == "confirm") {
            this.axios
              .post("/public/admin/admin/del", {
                ID: row.ID,
              })
              .then((res) => {
                if (res.data.code == 0) {
                  this.getAdminList();
                  this.$message("成功删除");
                } else if (res.data.code == -1) {
                  this.$message("删除失败，msg：" + res.data.msg);
                }
              });
          } else if (action == "cancel") {
            this.$message("取消操作");
          }
        },
      });
    },
    newAdmin() {
      console.log("new");
      this.axios.post("/public/admin/admin/add", this.form2).then((res) => {
        if (res.data.code == 0) {
          this.$message("成功新建管理员");
          this.getAdminList();
        } else if (res.data.code == -1) {
          this.$message("创建管理员失败，msg：" + res.data.msg);
        }
      });
    },
    newMatch() {
      this.$alert("新建互选场次将结束当前互选场次", "确认要创建新互选活动吗", {
        confirmButtonText: "确定",
        callback: (action) => {
          if (action == "confirm") {
            this.axios.post("/public/admin/command/newact").then((res) => {
              if (res.data.code == 0) {
                this.$message("成功新建活动场次");
                this.getMatchList();
              } else {
                this.$message("新建场次失败，msg：" + res.data.msg);
              }
            });
          }
        },
      });
    },
    jumpTo() {
      //跳转至第几期互选
    },
