    changeData(data) {
      let keyList = Object.keys(data);
      let valueList = Object.values(data);
      let objArr = [];

      for (let i = 0; i < keyList.length; i++) {
        let obj = new Object();
        let key = keyList[i];
        let val = valueList[i];
        obj["value"] = val;
        obj["name"] = key;
        objArr.push(obj);
      }
      console.log(objArr);
      return objArr;
    },
